html {
  font-family: system-ui, sans-serif;
  font-size: 1.25rem;
}

body {
  margin: 0;
  padding: 0 1.5rem;
}

svg {
  display: inline-block;
  vertical-align: middle;
}


.tooltip {
  opacity: 1 !important;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
    0 2px 4px -1px rgba(0, 0, 0, 0.06);
  letter-spacing: 0.025em;
}

.title {
  font-size: 2.25rem;
  margin-bottom: -1rem;
}

.state {
  font-weight: 500;
  font-size: var(--text-base);
}

.elect-total {
  font-size: var(--text-sm);
}

.eevp {
  font-style: italic;
  font-size: var(--text-sm);
  color: #718096;
}

.winner-name {
  font-weight: 600;
  font-size: var(--text-base);
  color: #2f855a;
}
